<p><br></p>
<p><span class="rvts58">猎人学派（</span><span class="rvts59">Huntsman</span><span class="rvts58">）</span><span class="rvts59">:</span>
<br><img width="128" height="128" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber318.jpg"><span class="rvts28"></span></p>
<p><span class="rvts21">猎人派系一共拥有</span><span class="rvts20">15</span><span class="rvts21">个技能。猎人学派技能基于角色的灵巧值（</span><span class="rvts20">Finess</span><span class="rvts21">）提供额外的伤害加成。</span>
<br><span class="rvts21">猎人学派大多数技能需要配合远程武器来增强远程物理攻击力。它有很多可以穿刺和破坏敌方护甲的技能。</span>
<br><span class="rvts43"></span>
<br><span class="rvts29">猎人学派技能书可以在以下</span><span class="rvts33">NPC</span><span class="rvts29">中买到：</span>
<br><span class="rvts20">1. </span><span class="rvts21">巴特（</span><span class="rvts20">Butter</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">欢乐堡</span>
<br><span class="rvts20">2. </span><span class="rvts21">巴哈拉（</span><span class="rvts20">Bahara</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿玛迪亚避难所</span>
<br><span class="rvts20">3. </span><span class="rvts21">商人哈南（</span><span class="rvts20">Trader Haran</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">浮木城</span>
<br><span class="rvts20">4. </span><span class="rvts21">精灵大树守卫（</span><span class="rvts20">Elf Treewarder</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">精灵营地，死神海岸</span>
<br><span class="rvts20">5. </span><span class="rvts21">弗莱彻</span><span class="rvts20"></span><span class="rvts21">科宾（</span><span class="rvts20">Fletcher Corbin Day</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">锯木厂</span>
<br><span class="rvts20">6. </span><span class="rvts21">游侠科姆</span><span class="rvts20"></span><span class="rvts21">哈德文（</span><span class="rvts20">Paladin Thom Hardwin</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">游侠桥，死神海岸</span>
<br><span class="rvts20">7. </span><span class="rvts21">黑环掠夺者（</span><span class="rvts20">Black Ring Reaver</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">费勒斯神庙南部，无名岛</span>
<br><span class="rvts20">8. </span><span class="rvts21">迷茫骑士（</span><span class="rvts20">The Lost Knight</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">泰森地留斯神庙，无名岛</span>
<br><span class="rvts20">9. </span><span class="rvts21">商人诺恩（</span><span class="rvts20">Trader Non</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿克斯</span>
